import React from 'react';
import { NavLink } from 'react-router-dom';

const Header = () => {
  return (
    <nav className="main-header">
      <ul>
        <li><NavLink to="/atlas">atlas</NavLink></li>
        <li><NavLink to="/info">info</NavLink></li>
      </ul>
    </nav>
  );
}

export default Header;